import React, { Component } from "react";
import { connect } from "react-redux";

class DemoMiniRedux extends Component {
  render() {
    return (
      <div>
        <button className="btn btn-success" onClick={this.props.tangSoLuong}>Tng</button>
        <span>{this.props.number}</span>
        <button className="btn btn-danger" onClick={this.props.giamSoLuong}>Gim</button>
      </div>
    );
  }
}
let mapStateToProps = (state) => {
  return {
    number: state.numberReducer.number,
  };
};
let mapDispatchToProps=(dispatch)=>{
  return {
    tangSoLuong:()=>{
      dispatch({
        type:"Tng",
        payload: 5,
      })
    },
    giamSoLuong:()=>{
      dispatch({
        type:"Gim",
        payload: 5,
      })
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DemoMiniRedux);
